<template>
  <div>
    <!-- 加载动画部分 -->
    <div v-if="isLoading" class="first-loading-wrp">
      <div class="loading-wrp">
        <span class="dot dot-spin">
          <i></i>
          <i></i>
          <i></i>
          <i></i>
        </span>
      </div>
      <h2>努力加载中...</h2>
    </div>

    <!-- 倒计时文本部分 -->
    <div v-else>
      <p>{{ countdown }}秒后自动跳转到{{ item[0].name }}...</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      countdown: 3,
      item: [
        { name: "教育管理平台", src: "http://58.50.22.13:9090/eams/loginExt.action" }
      ],
      isLoading: true // 用于控制加载动画的显示
    };
  },
  
  mounted() {
    this.startCountdown();
  },
  
  methods: {
    startCountdown() {
      const timer = setInterval(() => {
        this.countdown--;
        if (this.countdown <= 0) {
          clearInterval(timer);
          this.isLoading = false; // 停止显示加载动画
          window.location.href = this.item[0].src; // 跳转到目标页面
        }
      }, 1000);
    }
  }
};
</script>
